/* eslint-disable no-undef */
import React from 'react'
import { clone, is, equals } from 'ramda'
import envStore from '@/constants/env'
import hoist from 'hoist-non-react-statics'
import getDisplayName from '@/utils/get-display-name'

// https://stackoverflow.com/questions/57047722/how-to-disable-and-enable-tinymce-with-a-button-and-jquery

let defaultConfig = {
  block_formats: 'Heading 1=h1;Heading 2=h2;Heading 3=h3;Paragraph=p',
  inline: true,
  menubar: false,
  external_plugins: {
    dropdown: `${envStore.publicUrl}/js/tinymce/plugins/dropdown/plugin.js`,
    inputtext: `${envStore.publicUrl}/js/tinymce/plugins/inputtext/plugin.js`,
  },
  fontsize_formats: '10px 12px 14px 16px 18px 20px 24px 32px 36px',
  force_br_newlines: true,
  force_p_newlines: false,
  forced_root_block: 'div',
  plugins: 'image textcolor lists emoticons link table media code',
  tinymce: `${envStore.publicUrl}/js/tinymce/4.9.2/tinymce.min.js`,
  link_title: false,
  table_toolbar:
    'tablemergecells tablesplitcells | tableprops tabledelete | tableinsertrowbefore tableinsertrowafter tabledeleterow | tableinsertcolbefore tableinsertcolafter tabledeletecol',
  textcolor_cols: '5',
  content_security_policy: '',
  allow_unsafe_link_target: true,
  relative_urls: false,
  convert_urls: false,
  valid_elements: '*[*]',
  valid_children:
    'h1[div|strong|b],h2[div|strong|b],h3[div|strong|b],p[div|strong|b]',
  toolbar1:
    'undo redo | custom-formatselect fontsizeselect | bold italic underline strikethrough forecolor removeformat | align numlist bullist outdent indent | link | fontawesome | inputtext | dropdown',
}

export default function withTinyMCE(ComposedComponent) {
  class withTinyMCE extends React.Component {
    static displayName = `withTinyMCE(${getDisplayName(ComposedComponent)})`

    static defaultProps = {
      inline: false,
    }

    state = {
      top: 0,
      tabIndex: -1,
      editor: null,
      toolbarMouseOver: false,
      toolbarVisible: false,
    }

    componentDidMount() {
      if (this.props.inline) {
        this.initialize(this.props.item)
      }
    }

    componentDidUpdate(prevProps, prevState) {
      const { isDragging, inline, id, item } = this.props
      const { editor } = this.state

      if (!isDragging && prevProps.isDragging) {
        this.setState({ toolbarVisible: false })
      }
      if (!inline) {
        return
      }
      if (!equals(id, prevProps.id)) {
        this.initialize(item)
      }
      if (!equals(item?.html, prevProps.item?.html)) {
        editor.setContent(item?.html)
        this._addEventListeners(editor)
      }
    }

    _addEventListeners = (editor = this.state.editor) => {
      if (editor) {
        editor.dom.select('input').forEach(el =>
          el.addEventListener('input', function (evt) {
            const value = evt.target.value

            this.innerHTML = value
            this.setAttribute('data-mce-value', value)
            this.setAttribute('value', value)

            if (!value) {
              this.removeAttribute('data-mce-value')
              this.removeAttribute('value')
            }

            tinymce.triggerSave()
          })
        )

        editor.dom.select('select').forEach(el => {
          el.addEventListener('change', function (evt) {
            this.setAttribute('data-mce-value', evt.target.value)
            this.setAttribute('value', evt.target.value)

            this.childNodes.forEach(child => {
              if (!(child instanceof HTMLElement)) {
                return
              }
              if (child.innerText === evt.target.value) {
                child.setAttribute('selected', 'selected')
              }
              if (
                child.innerText !== evt.target.value &&
                child.hasAttribute('selected')
              ) {
                child.removeAttribute('selected')
              }
            })

            tinymce.triggerSave()
          })
        })
      }
    }

    initialize = snippet => {
      if (this._get(snippet.id)) {
        this.remove(snippet.id)
      }

      const _config = clone(snippet.config || defaultConfig)

      tinymce.init({
        ..._config,
        selector: `#${snippet.id}`,
        language_url: `${envStore.publicUrl}/js/tinymce/langs/zh_TW.js`,
        content: snippet.html,
        setup: editor => {
          editor.on('blur', this.onBlur)
          editor.on('focus', this.onFocus)
          // editor.on('change', this.onBlur)

          if (is(Function, _config.execInit)) {
            _config.execInit(editor)
          }

          if (snippet.html) {
            editor.on('init', () => editor.setContent(snippet.html))
          }
        },
        init_instance_callback: editor => {
          this.setState({ editor })
          this._addEventListeners(editor)
          editor.undoManager.add()
        },
      })
    }

    onFocus = () => {}

    onChange = () => {
      this.state.editor.getContent({ format: 'text' })
    }

    onBlur = e => {
      const { updateSnippet, item } = this.props
      // console.log(e.target.getContent({ format: 'text' }))
      if (updateSnippet) {
        updateSnippet({ ...item, html: e.target.getContent() })
      }
    }

    _get = id => {
      return tinymce.get(id)
    }

    remove = id => {
      tinymce.remove(this._get(id || this.props.id))
    }

    render() {
      return <ComposedComponent {...this.state} {...this.props} />
    }
  }

  return hoist(withTinyMCE, ComposedComponent)
}
